.App {
   height: 100%;
   margin: 0;
   display: flex;
   flex-direction: column;
}

#toolbar {
   padding: 0.75rem;
   border-radius: 1rem;
   border: 0.1rem solid black;
   border-left: none;
   /* background-color: #f1f1f1; */
   background-color: white;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   /* flex-direction: column; */
   gap: 1rem;
}

#whiteboard {
   border: 1px solid #000;
   flex-grow: 1;
   margin: 10px;
   /* Set a background color that does not conflict with the drawing color */
   background-color: #fff;
}

#clear {
   border-radius: 50%;
   height: 2.75rem;
   width: 2.75rem;
   display: flex;
   justify-content: center;
   align-items: center;
}

#eraser {
   border-radius: 50%;
   height: 2.75rem;
   width: 2.75rem;
   display: flex;
   justify-content: center;
   align-items: center;
}

#brush {
   border-radius: 50%;
   height: 2.75rem;
   width: 2.75rem;
   display: flex;
   justify-content: center;
   align-items: center;
}

#notepad {
   border-radius: 50%;
   height: 2.75rem;
   width: 2.75rem;
   display: flex;
   justify-content: center;
   align-items: center;
}

#clear img {
   width: 1.75rem;
   pointer-events: none;
}

#eraser img {
   width: 1.5rem;
   pointer-events: none;
}

#brush img {
   width: 1.5rem;
   pointer-events: none;
}

#notepad img {
   width: 1.5rem;
   pointer-events: none;
}

.toolbar-open {
   height: 100%;
   position: absolute;
   display: flex;
   flex-direction: row;
   justify-self: center;
   transition: all 200ms;
   align-items: center;
}

.toolbar-close {
   height: 100%;
   position: absolute;
   display: flex;
   flex-direction: row;
   justify-self: center;
   align-items: center;
   transition: all 200ms;
   transform: translateX(-4.5rem);
}

.arrow-close {
   transform: rotate(0deg);
   transition: all 200ms;
   border: none;
   background: none;
}

.arrow-open {
   transform: rotate(180deg);
   transition: all 200ms;
   border: none;
   background: none;
}

.arrow-icon {
   width: 2rem;
   height: 2rem;
   background-color: white;
}

.color-input-wrapper {
   height: 2.75rem;
   width: 2.75rem;
   border: 0.1rem solid black;
   overflow: hidden;
   border-radius: 50%;
   display: inline-flex;
   align-items: center;
   position: relative;
 }

 .color-input-wrapper  input[type=color] {
   position: absolute;
   height: 5rem;
   width: 5rem;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   overflow: hidden;
   border: none;
   margin: 0;
   padding: 0;
}

/* Circular cursor */
#circularcursor {
   background-color: transparent;
   border:1px solid black;    
   height:20px;
   width:20px;
   border-radius:50%;
   -moz-border-radius:50%;
   -webkit-border-radius:50%;
   position: absolute;
   z-index: 1000;
}